<template>
  <div class="hello">
    <input type="text" placeholder="Enter message" v-model="newMessage" v-on:keyup.enter="addMessage">
    <button type="submit" v-on:click="addMessage">Add message</button>
    <ul>
    	<li v-cloak v-for="message in messages">{{ message.text }}</li>
    </ul>
  </div>
</template>

<script>
/*global io*/
var socket = io()
export default {
  data () {
    return {
      messages: '',
      newMessage: ''
    }
  },
  ready () {
    // GET request
    this.$http.get('http://localhost:3030/messages')
    .then(function (response) {
      // set data on vm
      this.$set('messages', response.data.data)
    }, function (response) {
      // error callback
    })
  },
  created () {
    socket.on('messages created', function (message) {
      console.log('Got a new message!', message)
    })
  },
  methods: {
    addMessage () {
      var vm = this
      var text = vm.newMessage.trim()
      if (text) {
        vm.$http.post('http://localhost:3030/messages', {
          text: text
        })
        .then(function (response) {
          socket.emit('messages::create', { description: 'Do something' }, {}, function () {
            socket.emit('messages::find', {}, function (error, messages) {
              if (error) return 'Error'
              console.log(messages.data.text)
              // this.messages.push({ text: text })
              vm.messages = messages
              vm.newMessage = ''
            })
          })
        }, function (response) {
          // error
        })
      }
    }
  }
}
</script>



